<template>
    <div class="upload">
        <top></top>
        <div class="container pt-5 pb-5">
            <div class="w-75 m-auto">
                <div class="mb-3" @click="onSongClick" type="fileUpload" @dragenter="onDragenter" 
                    @dragover="onDragover" @dragleave="onDragleave" @drop="onDrop">
                    <div class="card" style="height: 400px;">
                        <div v-if="!song" class="m-auto">
                            <div>
                                <i class="mdi mdi-plus" style="font-size: 65px;"></i>
                            </div>
                            <div>
                                 
                            </div>
                        </div>
                        <div v-else class="h-100 justify-content-center d-flex align-items-center" style="background-repeat: no-repeat;background-size: contain;background-position: center" :style="{ 'background-image': `url(${thumbnail})` }">
                            <div class="">
                                <strong style="font-size: 30px;">{{ thumbnail ? '' : song.name }}</strong>
                            </div>
                        </div>
                    </div>
                </div>
                <div v-if="song">
                    <div v-if="song" class="text-left">
                        <button v-if="song"  class="btn mr-3" @click="removeSong"> </button>
                        <button v-if="!thumbnail" class="btn mr-3" @click="onThumbClick">  </button>
                        <button v-else class="btn" @click="removeThumb"> </button>
                    </div>
                </div>
                <hr>
                <div class="text-left form-group mb-5">
                    <div class="mb-3">
                        <div class="mb-2">Title</div>
                        <input type="text" class="form-control" v-model="metadata.title" placeholder="  .">
                    </div>
                    <div class="mb-3">
                        <div class="mb-2">Artist</div>
                        <input type="text" class="form-control" v-model="metadata.artist" placeholder="   .">
                    </div>
                    <div class="mb-3">
                        <div class="mb-2">Album</div>
                        <input type="text" class="form-control" v-model="metadata.album" placeholder="   .">
                    </div>
                    <div class="mb-3">
                        <div class="mb-2">Genre</div>
                        <input type="text" class="form-control" v-model="metadata.genre" placeholder="  .">
                    </div>
                    <div>
                        <div class="mb-2">Lyrics</div>
                        <textarea class="form-control" style="min-height: 100px;" v-model="metadata.lyrics" placeholder="  ."></textarea>
                    </div>
                </div>
                <input type="file" ref="coverInput" style="display: none;" @change="onThumbChange" accept="image/*">
                <input type="file" ref="fileInput" style="display: none;" @change="onSongChange" accept="audio/*, video/*">
                <div class="row">
                    <div class="col-md-6 mb-3">
                        <button class="btn-reverse w-75" @click="$router.push('/')"></button>
                    </div>
                    <div class="col-md-6">
                        <button class="btn w-75" @click="submitSong"></button>
                    </div>
                </div>
            </div>
        </div>
        <progress-modal v-if="isProgress" :msg="'  .\n .'"></progress-modal>
    </div>    
</template>

<script>
import top from '@/components/Nav.vue'

export default {
    name: 'Upload',
    components: {
        top,
    },
    data() {
        return {
            tmpAudio: null,
            isProgress: false,
            isDragged: false,
            song: null,
            thumbnail: null,
            metadata: {
                title: null,
                duration: null,
                genre: null,
                artist: null,
                album: null,
                lyrics: null,
            }
        }
    },
    beforeDestoy() {
        if (this.tmpAudio) this.tmpAudio.removeEventListener('loadedmetadata');
        this.tmpAudio = null;
    },
    methods: {
        onSongClick() {
            this.$refs.fileInput.click();
        },

        onThumbClick() {
            this.$refs.coverInput.click();
        },

        onDragenter() {
            this.isDragged = true;
        },

        onDragleave() {
            this.isDragged = false;
        },

        onDragover(e) {
            e.preventDefault();
        },

        onDrop(e) {
            e.preventDefault();
            this.isDragged = false;
            const files = e.dataTransfer.files;
            this.song = files;

        },

        loadAudioMetadata() {
            return new Promise((resolve, reject) => {
                let audioBlob = window.URL.createObjectURL(this.song);
                this.tmpAudio = new Audio();

                this.tmpAudio.addEventListener("loadedmetadata", () => {
                    resolve(this.tmpAudio.duration);
                })

                this.tmpAudio.src = audioBlob;
            })
        },

        async onSongChange(e) {
            this.thumbnail = null;
            const file = e.target.files;

            if (file.length) {
                this.song = file[0];
                this.metadata.title = this.song.name.split('.')[0];
                this.metadata.album = this.song.name.split('.')[0];

                this.metadata.duration = Math.floor(await this.loadAudioMetadata());

                e.target.value = '';
            }
        },        

        async onThumbChange(e) {
            const file = e.target.files;

            this.thumbnail = await this.readFile(file[0]);
            e.target.value = '';
        },

        removeThumb() {
            this.thumbnail = null;
        },

        removeSong() {
            this.song = null;
            this.thumbnail = null;
            this.metadata.title = null;
            this.tmpAudio = null;
        },

        async readFile(file) {
            return new Promise((resolve, reject) => {
                const reader = new FileReader();

                reader.onload = async (e) => {
                    resolve(e.target.result);
                }

                reader.readAsDataURL(file);
            })
        },

        async submitSong() {
            this.isProgress = true;

            if (!this.song || !this.metadata.title || !this.metadata.artist || !this.metadata.album) {
                this.isProgress = false;
                return false;
            }

            let formData = new FormData();
            formData.append('song', this.song);
            formData.append('metadata', JSON.stringify(this.metadata));
            formData.append('thumbnail', this.thumbnail);

            let res = await this.$Api.post('/api/music/upload', formData);

            if (res.data.success) {
                this.isProgress = false;
                window.alert('  .\n ,         .');
                this.$router.push('/');
            } else {
                this.isProgress = false;
                window.alert('  .\n     .');
            }
        },
    }
}
</script>

